.dragdroppable {
  position: relative;
}

// Fixes ISSUE-12181 - before in chart's contract-trigger breaks drag and drop mode
.dashboard--editing {
  .contract-trigger:before {
    display: none;
  }
}

.dragdroppable--dragging {
  opacity: 0.2;
}

.dragdroppable-row {
  width: 100%;
}

.dragdroppable-column {
  .resizable-container {
    span {
      div {
        z-index: @z-index-above-dashboard-charts;
      }
    }
  }
}

/* drop indicators */
.drop-indicator {
  display: block;
  background-color: @indicator-color;
  position: absolute;
  z-index: @z-index-above-dashboard-charts;
}

.drop-indicator--top {
  top: 0;
  left: 0;
  height: 4px;
  width: 100%;
  min-width: 16px;
}

.drop-indicator--bottom {
  top: 100%;
  left: 0;
  height: 4px;
  width: 100%;
  min-width: 16px;
}

.empty-droptarget:first-child {
  .drop-indicator--bottom {
    top: 24px;
  }
}

.drop-indicator--right {
  top: 0;
  left: 100%;
  height: 100%;
  width: 4px;
  min-height: 16px;
}

.drop-indicator--left {
  top: 0;
  left: 0;
  height: 100%;
  width: 4px;
  min-height: 16px;
}

/* empty drop targets */
.dashboard-component-tabs-content {
  & > .empty-droptarget {
    position: absolute;
    width: 100%;
  }

  & > .empty-droptarget:first-child {
    height: 14px;
    top: -2px;
    z-index: @z-index-above-dashboard-charts;
  }

  & > .empty-droptarget:last-child {
    height: 12px;
    bottom: 0px;
  }
}

.grid-content {
  /* note we don't do a :last-child selection because
    assuming bottom empty-droptarget is last child is fragile */
  & > .empty-droptarget {
    width: 100%;
    height: 100%;
  }

  & > .empty-droptarget:first-child {
    height: 48px;
    margin-top: -24px;
    margin-bottom: -24px;
  }

  & > .empty-droptarget:only-child {
    height: 80vh;
  }
}
