}
.react-bs-container-body {
  max-height: 400px;
  overflow-y: auto;
}
.hidden,
#pageDropDown {
  display: none;
}
.separator .chart-container {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}
.dashboard .title {
  margin: 0 20px;
}
.slice_container .alert {
