.dashboard-component-chart-holder {
  width: 100%;
  height: 100%;
  color: #333333;
  background-color: #ffffff;
  position: relative;
  padding: 16px;
  overflow-y: visible;
  transition: opacity 0.2s, border-color 0.2s, box-shadow 0.2s;
  border: 2px solid transparent;
}
.dashboard-component-chart-holder .missing-chart-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;
  justify-content: center;
}
.dashboard-component-chart-holder .missing-chart-container .missing-chart-body {
  font-size: 12px;
  position: relative;
  display: flex;
}
.dashboard-component-chart-holder.fade-in {
  border-radius: 4px;
  box-shadow: inset 0 0 0 2px #20a7c9, 0 0 0 3px rgba(32, 167, 201, 0.1);
  transition: box-shadow 0.2s ease-in-out, opacity 0.2s ease-in-out, border-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
}
.dashboard-component-chart-holder.fade-out {
  border-radius: 4px;
  box-shadow: none;
  transition: box-shadow 0.2s ease-in-out, opacity 0.2s ease-in-out, border-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
}
.dashboard-chart {
  overflow: hidden;
  position: relative;
}
.dashboard-chart.dashboard-chart--overflowable {
  overflow: visible;
}
.dashboard--editing .dashboard-component-chart-holder:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  z-index: 1;
  pointer-events: none;
  border: 1px solid transparent;
}
.dashboard--editing .dashboard-component-chart-holder:hover:after {
  border: 1px dashed #20a7c9;
  z-index: 2;
}
.dashboard--editing .resizable-container:hover > .dashboard-component-chart-holder:after,
.dashboard--editing .resizable-container.resizable-container--resizing:hover > .dashboard-component-chart-holder:after {
  border: 1px dashed #20a7c9;
}
.dashboard--editing .resizable-container .dashboard-component-chart-holder .dashboard-chart .chart-container {
  cursor: move;
  opacity: 0.2;
}
.dashboard--editing .resizable-container .dashboard-component-chart-holder .dashboard-chart .slice_container {
  /* disable chart interactions in edit mode */
  pointer-events: none;
}
.dashboard--editing .resizable-container .dashboard-component-chart-holder:hover .dashboard-chart .chart-container {
  opacity: 0.7;
}
.dot {
